import external from '../externalModules.js';
import BaseTool from './base/BaseTool.js';
import sharpnessUtils from '../util/sharpness/index.js';
import { probeCursor } from './cursors/index.js';

const { changeViewportpixelReplication } = sharpnessUtils;

/**
 * @public
 * @class SharpnessTool
 * @memberof Tools
 *
 * @classdesc Tool for changing magnification.
 * @extends Tools.Base.BaseTool
 */
export default class SharpnessTool extends BaseTool {
  constructor(props = {}) {
    const defaultProps = {
      name: 'Sharpness',
      strategies: { defaultStrategy },
      supportedInteractionTypes: ['Mouse', 'Touch'],
      svgCursor: probeCursor,
    };
    console.log('temptemp');

    // Calculate the new scale factor based on how far the mouse has changed
    super(props, defaultProps);
  }
}

/**
 * The default strategy changes pixelReplication. True -> False, False -> True
 *
 *
 * @param {*} evt
 * @param {*} { invert, maxScale, minScale }
 * @returns {void}
 */
function defaultStrategy(evt) {
  const { element, viewport } = evt.detail;
  console.log('sharpeness default strage', viewport);

  // Calculate the new scale factor based on how far the mouse has changed
  const updatedViewport = changeViewportpixelReplication(viewport);
  external.cornerstone.setViewport(element, updatedViewport);
}
